import { Component }                                            from '@angular/core';
import { IonicPage, NavController, NavParams }  from 'ionic-angular';
import { Contacts, Developments , User, Properties                        } from '../../providers';
import { FormBuilder, FormGroup, Validators                   } from '@angular/forms';
import { CallNumber } from '@ionic-native/call-number';


@IonicPage()
@Component({
  selector: 'page-contact',
  templateUrl: 'contact.html',
})
export class ContactPage {

  btnAdd      = false;
  spinner     = false;
  public type : any ;
  data : any  = [] ;
  msg         = 'Enviar' ;
  public item : any = [];
  public commentForm: FormGroup; 
  btnCall = false;

  constructor(private callNumber: CallNumber,public properties: Properties, public development : Developments, public user : User, public formBuilder: FormBuilder, public contact: Contacts, public navCtrl: NavController, public navParams: NavParams) {

    if (this.navParams.data == 2){
      this.type = 2
    } else if (this.navParams.data == 3){
      this.type = 3
    }else{
      this.type  = 1;
    }

    if (this.type == 2 || this.type == 3) {
      this.item = this.properties.property;
      this.item.developmentNameEs = this.properties.property.propertyNameEs;
      ////console.log(this.item);
    } else {
      this.item = this.development.development;
    } 

    
    this.commentForm = formBuilder.group({
      comment: ['', [Validators.required]]
    });

  }
 
  ionViewDidLoad() {
    ////console.log('load');
    ////console.log(this.properties.property);
    if (this.type == 2 || this.type == 3 ){
      this.item = this.properties.property;
      this.item.developmentNameEs = this.properties.property.propertyNameEs;
      ////console.log(this.item);
    }else{
      this.item = this.development.development;
    }

  }

  call(number) {
    ////console.log('llamar al numero ', number);
    if (this.btnCall)
      return;
    this.btnCall = true;

    this.callNumber.callNumber(number, true)
      .then(res => this.btnCall = false)
      .catch(err => this.btnCall = false);

  }
  submit(){

    if(this.btnAdd)
      return;

    this.btnAdd   = true;
    this.spinner  = true;
    var $this     = this;
    var response : any;
    this.msg = '';
    if (this.type == 2 || this.type == 3){
      ////console.log('propety');
      response = this.properties.infoProperty(this.data);

    }else{
      ////console.log('dev');
      response = this.development.infoDevelopment(this.data);

    }

    Promise.all([response]).then(data => {
      
      this.spinner  = false;
      this.msg      = 'Â¡Listo!';

      setTimeout(()=>{

        $this.btnAdd = false;
        $this.navCtrl.pop();
      
      }, 1000); 

    }); 
  }

  cancel(){
    this.navCtrl.pop();
  }

}



